<template>
  <div id="home">
    <!-- <template>
      <mt-header title="全民经纪人">
        <mt-button slot="right" @click = "gotoLogin()">登录</mt-button>
        </mt-header>search_not
    </template> -->

    <mt-header title="问答频道">
      <!-- <mt-button slot="right" @click = "gotoLogin()">登录</mt-button> -->
      <div slot="right" class="user" @click="gotoAddress('myinfo',true)"><img :src="getUserAvatar"></div>
    </mt-header>
    <section class="header_search">
      <!-- <div class="user" @click="gotoAddress('myinfo',false)"><img :src="getUserAvatar"></div> -->
      <div class="search">
        <img src="../../images/myinfo/search_not.png">
        <input type="search" placeholder="房产知识有疑问？来搜搜吧~" v-model="searchStr" />
      </div>
      <i @click="gotoSearch()">搜索</i>
    </section>

    <!-- 滚动广告 -->
    <mt-swipe :auto="4000" id="mySwipe" v-if="swipeData.length">
      <mt-swipe-item v-for = "(item, index) in swipeData" class="swiperItem">
          <img @click = "handleSwipe(index)" :src="'//p.07358.com'+item.pic_url">
      </mt-swipe-item>
      <i>广告</i>
    </mt-swipe>

    <section class="nav_wrap">
      <nav @click="gotoCategory({name:'新房',type:1})">
        <div><img src="../../images/home/nav_0.png"><span>新房</span></div></nav>

      <nav @click="gotoCategory({name:'商业',type:10})">
        <div><img src="../../images/home/nav_4.png"><span>商业</span></div></nav>

      <nav @click="gotoCategory({name:'二手房',type:2})">
        <div><img src="../../images/home/nav_1.png"><span>二手房</span></div></nav>

      <nav @click="gotoCategory({name:'评估',type:3})"><div><img src="../../images/home/nav_2.png"><span>评估</span></div></nav>

      <nav @click="gotoCategory({name:'法律',type:4})">
        <div><img src="../../images/home/nav_3.png"><span>法律</span></div></nav>
    </section>

    <!-- 10线条 -->
    <div class="lise_10"></div>

    <section class="expert">
      <div class="header_title">
        <h2>热门专家</h2>
        <div class="mero" @click="gotoAddress('expertlist')"><span>查看更多</span><img src="../../images/home/Arrow_home.png"></div>
      </div>
      <div class="list">
        <ul>
          <li v-for="item in expertArr" @click="gotoExpert(item)">
              <!-- <img  v-if="item.avatarPath" :src="imgBaseUrl+item.avatarPath">
              <img  v-if="!item.avatarPath" src="http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y">
              <span>{{item.trueName|getName}}</span>
              <p class="sub" v-if="item.tag">{{item.tag}}</p> -->
              <expertItem :itemData="item"></expertItem>
          </li>
        </ul>
      </div>
    </section>
    <!-- 10线条 -->
    <div class="lise_10"></div>

    <section class="hot_ask">
      <div class="header_title"><h2 style="margin-left:10px;">热门问题</h2></div>
      <div class="list">
        <li v-for="item in list" @click="gotoDetail(item)">
          <router-link :to="{path: '/askDetail', query:{qid: item.q_id}}">
            <AskList  :itemData="item"></AskList>
          </router-link>
          <!-- <h2><img src="../../images/home/hot_icon.png">{{item.q_title}}</h2>
          <p>{{item.q_content}}</p>
          <div class="bottom">
            <span><em>浏览 {{item.q_hits}}</em></span>
            <span><em>回答 {{item.q_replys}}</em></span>
            <span><em>收藏 {{item.q_cang}}</em></span>
          </div> -->
        </li>
      </div>
    </section>


    <!-- <section class="ask_list">
      <li v-for="item in list" >
      <router-link :to="{path: '/askDetail', query:{qid: item.q_id}}">
        <AskList  :itemData="item"></AskList>
      </router-link>
      </li>
    </section> -->

    <i class="myPublishAsk" @click="gotoAddress('/publishAsk',true)">提问</i>

    <div class="noData" v-if="list.length == 0">数据为 {{list.length}} 条</div>
    <div @click="seeing">
      <div class="loading-start" v-if="loading"></div>
      <span class="more" v-if="!complete && !loading">点击加载更多</span>
    </div>

    <!-- 65线条 -->
    <div class="lise_65"></div>

    <!-- 底部的 tabBar -->
    <mt-tabbar fixed>
      <mt-tab-item id="问答" @click.native="gotoAddress('/home')"><img slot="icon" src="../../images/main/n1_select.png">问答</mt-tab-item>
      <mt-tab-item id="百科" @click.native="gotoAddress('/baike')"><img slot="icon" src="../../images/main/n2.png">百科</mt-tab-item>
    </mt-tabbar>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import AskList from '../../page/home/children/AskList'
import expertItem from './children/expertItem'
import $ from 'webpack-zepto'
import httpTool from '../../utils/httpTool'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userInfo:null,
      isLogin:false,
      swipeData:[
        {
          "img": "//p.07358.com/cfw/20170629/20170629160121_63143.jpeg",
          "guid": "adv0",
          "title": "郴房网",
          "href": "http://www.07358.com/zhuanti/2017/cfzb5/"
        },
        {
          "img": "//p.07358.com/cfw/20170722/20170722112037_87030.jpg",
          "guid": "adv0",
          "title": "郴房网",
          "href": "http://www.07358.com/zhuanti/2017/cfzb5/"
        }
      ],
      searchStr:'',
      list:[],
      expertArr:[],
      complete: false, // 数据是否加载完成
      loading: false, // 是否在请求中
      pageSize:10, // 默认显示10数据
      lbArr:[],
      hotAsk:[],
      askZ:1,
      isGet:true,
      searchKey:{
        page:1,
        zid:1,
        complete:false,
      },
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
    getName(name){
      if (name == '') {
        return '郴房网专家'
      }else {
        return name
      }
    }
  },
  mounted(){
    this.initData()

    // 获取滚动广告
    this.getSwipeData()

    // 获取热门问题
    // this.getHotAsk()
    this.getData()

    // 获取热门专家
    this.getExpert()

    if (this.userID) {
      this.isLogin = true
      this.getUserData()
    }
    // this.pageKey = this.$route.name
    // if (window.window.sessionStorage.pageKey == this.pageKey) {
    //     this.list = JSON.parse(window.window.sessionStorage.List);
    //     this.searchKey = JSON.parse(window.window.sessionStorage.searchKey);
    //     this.complete = this.searchKey.complete
    //     // 当时 = 0 的时间不触发监听不知道为什么
    //     if (this.searchKey.selected != 0) {
    //       // 当触发监听时因为这是从缓存读的所以不用 getData
    //       this.isGet = false
    //     }
    //     this.zid = this.searchKey.zid
    //     this.$nextTick(() => $(window).scrollTop(window.window.sessionStorage.scrollTop));
    // } else {
    //     //this.selected = this.$route.query.sid ? this.$route.query.sid+'': '0';
    //     this.getData()
    // }


  },
  components:{
    AskList,
    expertItem
  },
  computed:{
    // http://qpic.07358.com/FiPMqTlrYzi_8naGgKIfj4h9wGLi
    // http://qpic.07358.com/FjfhK3PXrdbdmtvH736VrIw4vbZf
    getUserAvatar:function(){
      if(this.userInfo){
        let indexOf = this.userInfo.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.userInfo.avatarPath
        }else {
          return imgBaseUrl + this.userInfo.avatarPath
        }
      }else {
        return "http://qpic.07358.com/FiPMqTlrYzi_8naGgKIfj4h9wGLi"
      }
    }
  },
  beforeRouteEnter (to, from, next) {
     // 在渲染该组件的对应路由被 confirm 前调用
     // 不！能！获取组件实例 `this`
     // 因为当钩子执行前，组件实例还没被创建
     if (from.name != 'AskDetail') {
       window.window.sessionStorage.removeItem('List');
       window.window.sessionStorage.removeItem('pageKey');
       window.window.sessionStorage.removeItem('scrollTop');
     }
     next()
   },
   beforeRouteLeave(to, from, next) {
     // 导航离开该组件的对应路由时调用
     // 可以访问组件实例 `this`
     if (to.name == 'AskDetail') {
        // 当前滚动条位置
        window.window.sessionStorage.scrollTop = $(window).scrollTop()
        // 当前页面主题数据
        window.window.sessionStorage.List = JSON.stringify(this.list)
        // 保存页面key
        window.window.sessionStorage.pageKey = this.pageKey
        // 当前tab
        this.searchKey.complete = this.complete
        this.searchKey.zid = this.zid
        window.window.sessionStorage.searchKey = JSON.stringify(this.searchKey)
      }
      $(window).off('scroll');
     next()
  },
  methods: {
    async initData(){
      // try{
      //   let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'
      //   let result = await httpTool(getUrl);
      //   console.log(result)
      // }catch(err){
    	// 	console.log(err);
    	// }
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login')
        }
      }else {
          this.$router.push(path)
      }
    },
    gotoCategory(params){
      let name = encodeURI(encodeURI(params.name));
      this.$router.push({path: '/category', query:{tid: params.type,name:name}})
    },
    gotoSearch(){
      let str = encodeURI(encodeURI(this.searchStr));
      this.$router.push({path: '/search', query:{keyStr:str,type:1}})
    },
    gotoExpert(item){
      let p = {path: '/expert', query:{eid: item.id}}
      this.$router.push(p)
    },
    gotoDetail(item){
      let p = {path: '/askDetail', query:{qid: item.q_id}}
      this.$router.push(p)
    },
    gotoLogin(){
      this.$router.push('/login')
    },
    handleAskZ(idx){
      this.askZ = idx
      this.searchKey.page = 1;
      this.getData()
    },
    // 获取网络数据
    getData(){
      Indicator.open();
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'+pStr
      // console.log(getUrl)
      if (this.searchKey.page == 1) {
        this.list = []
      }
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.list.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 获取热门问题
    getHotAsk(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/hot'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.hotAsk = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    // 获取热门专家
    getExpert(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/syzj'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          this.expertArr = []
          this.expertArr = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    getUserData(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/user?token='+this.userToken
      this.$http.post(getUrl).then(response => {
        if (response.data.code == 200) {
          this.userInfo = response.data.result
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    // 获取滚动广告
    getSwipeData(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v2/adv/news?navId=500'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.status
        if (code == 200) {
          this.swipeData = response.body
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    seeing () {
      if (this.searchKey.page === 1) {
        return // 防止偶然性的第一个重复请求的bug
      }
      this.getData()
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
    	if (goUrl != 1){
        console.log('pc')
    		this.$router.push('index')
    	}else {
    	  console.log('Mobile')
    	}
    }
  },

}
</script>

