<template>
  <div class="expertItem">
    <img :src="getUserAvatar">
    <!-- <img  v-if="!itemData.avatarPath" src="http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"> -->
    <span>{{itemData.trueName|getName}}</span>
    <p class="sub" >{{getImgs}}</p>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
import CellLabel from './CellLabel'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    },
    getName(name){
      if (name == '') {
        return '郴房网专家'
      }else {
        return name
      }
    },
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData.avatarPath){
        let indexOf = this.itemData.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.itemData.avatarPath
        }else {
          if (this.itemData.avatarPath == '          ') {
            return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
          }else {
            let indexOf = this.itemData.avatarPath.indexOf('/users')
            if (indexOf) {
              return '/p.07358.com' + this.itemData.avatarPath
            }else {
              return imgBaseUrl + this.itemData.avatarPath
            }
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getImgs(){
      let imags = this.itemData.tags.split(',')
      if (imags[0]=='') {
        return '擅长'
      }else {
        return imags[0];
      }

    }
  },
  created(){
  },
  mounted(){
  },
  components:{
    CellLabel
  },
  methods: {

  }

}

</script>

<style scoped>
.expertItem{
  /*width: 90px;*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.expertItem img{
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: 1px solid #eee;
}

.expertItem div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5px;
  /*width: 80px;*/
}

.expertItem span{
  padding: 5px 0px;
  font-size: 13px;
  font-weight: 700;
  color: #000;
}

.expertItem .sub{
  font-size: 10px;
  border: 1px solid #eee;
  padding: 3px 8px;
  line-height: 14px;
  border-radius: 13px;
  /*min-height: 16px;*/
}

</style>



